<template>
<!--  -->
  <div>
    <!-- 头部 -->
    <header class="fl-ex al-it">
      <router-link to="/societyInstitution" tag="img" src="../../../static/img/29.png" alt="图"></router-link>
      <p>社会机构管理</p>
    </header>
    <!-- 工会信息 -->
    <main v-for="(item,index) in list" :key="'info1-'+index">
      <div class="chairman">
        <div class="list" v-for="(s,index) in item.lists" :key="index">
          <div class="fl-ex al-it ju-be">
            <p>{{s.p}}</p>
            <span>{{s.span}}</span>
          </div>
        </div>
        <div style="width:100%; background-color:white;" v-for="(m,index) in item.img" :key="'info2-'+index">
          <div class="fl-ex al-it ju-be">
            <p>{{m.p}}</p>
            <span>{{m.span}}</span>
          </div>
          <a>{{m.wenzi}}</a>
          <div class="fl-ex al-it ju-be">
            <p>{{m.p1}}</p>
            <span>{{m.span1}}</span>
          </div>
          <img :src="m.pic" />
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          lists: [
            {
              p: "组织类别",
              span: "社工机构"
            },
            {
              p: "组织代码",
              span: "1035620"
            },
            {
              p: "组织名称",
              span: "雨之露社工"
            },
            {
              p: "联系人",
              span: "王磊"
            },
            {
              p: "联系方式",
              span: "18297302248"
            },
            {
              p: "创立日期",
              span: "2019-03-15"
            },
            {
              p: "所在地",
              span: "西宁"
            },
          ],
          img: [
            {
              p: "组织介绍",
              span: "",
              p1: "宣传海报",
              span1: "",
              wenzi:'本次活动以提倡变费为宝，可持续发展的环保理念为导向，目的在于使社区居民深入了解环保酵素的概念，环保酵素的用途以及环保酵素的做法。环保酵素不但能够帮助降解生活中食物上农药残留的化学品，而且还能处理蚊虫叮咬，轻微烫伤，制作初期，需要每天打开瓶盖，让发酵的气体往外流通，如果没有打开瓶口，气体溢满无法释放时，甚至会爆炸，制作的时候最好用塑料瓶，避免用玻璃瓶，防止爆炸。发酵期3个月后可使用，6个月或以上更好，发酵期越久，效果越佳，越淳。环保酵素不但制作简单，制作材料也随手可得，而且用途广泛，还可以帮助减少垃圾量，对环保起着很大作用，社工将环保酵素制作的注意事项一一介绍给参加活动的居民，并指导居民制作环保酵素，大家也积极的同社工互动。',
              pic: "../../../static/img/48.png"
            },
          ]
        },
      ]
    };
  }
};
</script>

<style scpoed>
header {
  width: 100%;
  height: 3.2rem;
  background-color: #df0001;
}
header img {
  width: 4%;
  height: 1.25rem;
  margin-left: 5%;
}
header p {
  font-size: 1.2rem;
  color: white;
  margin-left: 28%;
}
/* 工会信息  */
main {
  width: 100%;
}
main .chairman {
  width: 100%;
  background-color: #e2e2e2;
}
main .chairman img {
  width: 93%;
  height: 12rem;
  margin: 0 auto;
  margin-top: 5%;
  padding-bottom: 3%;
}
main .chairman .list {
  width: 100%;
  height: 2.5rem;
  background-color: white;
}
main .chairman:last-child {
  padding-bottom: 0.5rem;
}
main .chairman > div > div {
  width: 94%;
  height: 2rem;
  margin: 0 auto;
  border-bottom: 0.05rem solid #e2e2e2;
}
main .chairman > div > div p {
  font-size: 0.8rem;
  color: #8c8c8c;
}
main .chairman > div > div span {
  font-size: 0.8rem;
  color: #393939;
}
main a {
  font-size: 0.87rem;
  line-height: 1.3rem;
  text-indent: 2rem;
  letter-spacing: 0.05rem;
  text-align: left;
  width: 95%;
  margin: 0 auto;
  color: #313131;
  font-weight: bold;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
</style>


